




























import dayjs from 'dayjs'
import Vue from 'vue'

export default Vue.extend({
  name: 'ListView',
  props: {
    boards: {
      type: Array,
      required: true
    },
    isLoading: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    getDay (time: number) {
      return dayjs(time).format('YYYY-MM-DD')
    }
  }

})
