
















import Vue from 'vue'
import ListView from './component/ListView.vue'

export default Vue.extend({
  components: { ListView },
  created () {
    this.init()
  },
  beforeDestroy () {
    this.$store.commit('setBoards', [])
  },
  methods: {
    init () {
      this.$store.dispatch('GET_BOARDS')
    },
    boardCreate () {
      this.$router.push('/board/create')
    }
  },
  computed: {
    list () {
      return this.$store.state.Board.boards
    }
  }
})
